body {
  margin: 0;
  padding: 20px;
  background-color: #fff;
  color: #2a2a2a;
  line-height: 1.3;
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin-top: 0;
}

.Container {
  padding: 10px;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid black;
  border-radius: 3px;
}
.Contacts {
  display: inline-flex;
  flex-direction: column;
  margin-top: 10px;
  padding: 10px;
  width: 350px;
  border: 1px solid lightgray;
}
